@import "../../scss/variable";

#activity,
#activityClub {
     margin-top: 50px;
     @include flex(center);
     flex-direction: column;
     @media screen and (max-width: 500px) {
          margin-top: 0px;
     }
}
.ac {
     padding: 0px 20px;
     &__container {
          -ms-overflow-style: none;
          scrollbar-width: none;
     }
     &__container::-webkit-scrollbar {
          display: none;
     }
     &__container {
          @include flex(center);
          flex-wrap: wrap;
          transition: 0.3s cubic-bezier(0, 0.37, 0.85, 1.79);

          width: 90%;
          gap: 20px;
          @media screen and (max-width: 1280px) {
               gap: 30px;
          }
          @media screen and (max-width: 500px) {
               padding: 10px 25px;
               gap: 20px;
               justify-content: flex-start;
               flex-direction: row;
               flex-wrap: nowrap;
               overflow-x: auto;
               transition: 0.25s cubic-bezier(0, 0.37, 0.85, 1.79);
               width: 100%;
               overflow-x: auto;
               scroll-snap-type: x mandatory;
               scroll-snap-stop: always;
               -ms-touch-action: manipulation;
               touch-action: manipulation;
               -webkit-overflow-scrolling: touch;
               &.active1 {
                    padding: 0px;
                    gap: 20px;
                    justify-content: center;
                    flex-direction: column;
                    flex-wrap: wrap;
                    overflow-x: unset;
               }
          }
     }
     &__box {
          box-sizing: border-box;
          border-radius: 4px;
          background-color: white;
          @include flex(around);
          flex-direction: column;
          width: 330px;
          height: 250px;
          transition: 0.25s cubic-bezier(0, 0.37, 0.85, 1.79);
          box-shadow: 0 0 3px $mainClr;
          overflow: hidden;
          scroll-snap-align: center;
          scroll-snap-stop: always;

          @media screen and (max-width: 767px) {
               width: 380px;
               height: 250px;
          }
          @media screen and (max-width: 467px) {
               width: 310px;
          }
          &--description {
               @include abs(center);
               color: #333;
               width: 300px;
               padding: 10px;
               text-align: justify;
               white-space: pre-wrap;
               line-height: 1.6;
               transition: 0.3s ease-in;
               opacity: 0;
               font-size: 14px;
               @media screen and (max-width: 767px) {
                    line-height: 1.5;
                    font-size: 13px;
               }
          }

          &--img {
               width: 100%;
               height: 83%;
               position: relative;
               margin-top: -20px;
               transition: 0.3s ease;
               user-select: none;

               & img {
                    @include abs(full);
                    transition: 0.3s ease-in;
                    object-fit: fill;
                    pointer-events: none;
                    user-select: none;
                    opacity: 0;
                    border-bottom: 1px solid $mainClr;
               }
          }
          &--title {
               color: $TextClr2;
               font-weight: 500;
               transition: 0.3s cubic-bezier(0, 0.37, 0.85, 1.79);
               padding: 4px 5px;
               border-radius: 5px;
               font-size: 18px;
               transform: translateY(-2px);
          }
          @media screen and (max-width: 768px) {
               &.active {
                    background-color: $background;
                    box-shadow: 0 0 3px $TextClr;
                    transform: scale(1.01);
                    & .ac__box--title {
                         color: $background;
                         box-shadow: 0 0 3px $mainClr;
                         border: 1px solid $newMain3;
                         text-shadow: none;
                         background-color: $newClr4;
                         border: 1px solid $mainClr;
                         padding: 0;
                         transform: translateY(-12px);
                         cursor: pointer;
                         padding: 4px 6px;
                         border-radius: 5px;
                         font-size: 15px;
                    }
                    & .ac__box--description {
                         opacity: 1;
                    }
                    &.ac__box--img {
                         width: 100%;
                    }

                    & .ac__box--img img {
                         opacity: 0 !important;
                    }
               }
          }
          @media screen and (min-width: 768px) {
               &:hover {
                    background-color: $background;
                    box-shadow: 0 0 3px $mainClr;
                    transform: scale(1.01);

                    & .ac__box--title {
                         color: $background;

                         text-shadow: none;
                         background-color: $newClr4;
                         border: 1.5px solid $newMain3;

                         padding: 0;
                         transform: translateY(-12px);
                         cursor: pointer;
                         padding: 4px 6px;
                         border-radius: 5px;
                         font-size: 15px;
                    }
                    & .ac__box--description {
                         opacity: 1;
                    }
                    &.ac__box--img {
                         width: 100%;
                    }
                    & .ac__box--img img {
                         opacity: 0 !important;
                    }
               }
          }
     }
}
.hide {
     opacity: 1 !important;
}
.ac__ques {
     &.active {
          z-index: 9999;
          transform: scale(1);
          opacity: 0.98;
          transform: translate(-50%, -50%);
     }
     position: fixed;
     top: 50%;
     left: 50%;
     transition: 0.25s ease-in-out;
     width: 350px;
     height: 200px;
     background-color: $background;
     transform-origin: 0 0;
     display: flex;
     align-items: center;
     justify-content: center;
     border-radius: 5px;
     color: $TextClr;
     z-index: -5;
     box-shadow: 0 0 3px $mainClr;
     transform: scale(0.005);
     opacity: 0;
     @media screen and (max-width: 767px) {
          width: 300px;
          height: 150px;
          top: 45%;
     }

     &__container {
          @include flex(center);
          gap: 60px;
          flex-direction: column;
     }
     &__btns {
          display: flex;
          gap: 100px;
     }
     &__btn {
          padding: 8px 12px;
          box-shadow: 0 0 3px $mainClr;
          transition: 0.25s ease-in-out;
          color: white;
          background-color: $newClr4;
          border-radius: 5px;
          border: none;

          &:hover {
               background-color: $background;
               color: $mainClr;
          }
     }
}
.ac__modal {
     position: fixed;
     width: 100%;
     height: 100%;
     top: 0;
     left: 0;
     background-color: rgb(5, 19, 0);
     opacity: 0;
     z-index: -5;
     transition: 0.3s ease-in-out;
     &.active {
          opacity: 0.4;
          z-index: 999;
     }
}
.ac__next__title {
     font-weight: 500;
}
.button-wrap {
     background-color: $newMain;
     padding: 3px 5px;
     color: $background;
     margin-left: 12px;
     box-shadow: 0 0 3px $TextClr;
     border-radius: 4px;
     display: none;
     transition: 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
     &.active {
          background-color: $background;
          box-shadow: 0 0 3px $TextClr;

          color: $newMain;
     }
     @media screen and (max-width: 500px) {
          display: block;
     }
     &.active {
          background-color: $background;
          box-shadow: 0 0 3px $TextClr;

          color: $newMain;
     }
}
.ac__heading {
     &-box {
          margin: 30px 0;
          margin-top: 50px;
          @include flex(center);
     }
     &-item {
          color: $TextClr;
          font-size: 34px;
          position: relative;
          @media screen and (max-width: 767px) {
               font-size: 24px;
          }
          @media screen and (max-width: 367px) {
               font-size: 18px;
          }
          & span {
               position: absolute;
               bottom: -7px;
               left: -10px;
               height: 2px;
               width: 20%;
               background-color: $mainClr;
          }
     }
}
