@import "../../scss/variable";

#main-slider {
     @include flex(center);
     margin-top: 80px;
     flex-direction: column;
     position: relative;
}
.slider__box__container {
     position: relative;
     display: flex;
     justify-content: center;
     align-items: center;
     width: 70%;
     height: 600px;
     cursor: grab;

     @media screen and (max-width: 1200px) {
          width: 95%;
          height: 530px;
     }
     @media screen and (max-width: 767px) {
          width: 95%;
          height: 420px;
     }
     @media screen and (max-width: 600px) {
          width: 95%;
          height: 320px;
     }
     @media screen and (max-width: 467px) {
          width: 95%;
          height: 230px;
     }
     &:active {
          cursor: grabbing;
     }
}
