.m-slider {
     &__container {
          margin-top: 50px;
          width: 100%;
          height: 100%;
          object-fit: cover;
          @include flex(center);
          position: absolute;
          overflow: hidden;
          border-radius: 5px;
          box-shadow: 0 0 7px $mainClr;
     }
     &__images {
          -ms-overflow-style: none;
          scrollbar-width: none;
     }
     &__images::-webkit-scrollbar {
          display: none;
     }
     &__images {
          width: 100%;
          height: 100%;
          display: flex;
          position: relative;
          transition: 0.25s ease;

          & img {
               width: 100%;
               scroll-snap-align: start;
               scroll-snap-stop: always;
          }
          @media screen and (max-width: 767px) {
               transition: 0.15s cubic-bezier(0, 0.37, 0.85, 1.79);
               width: 100%;
               overflow-x: auto;
               scroll-snap-type: x mandatory;
               scroll-snap-stop: always;
               -ms-touch-action: manipulation;
               touch-action: manipulation;
               -webkit-overflow-scrolling: touch;
          }
     }
     &__buttons {
          @include abs(center);
          display: flex;
          top: 55%;
          width: 110%;
          justify-content: space-between;
          @media screen and (max-width: 767px) {
               display: none;
          }
     }
     &__button {
          &:active {
               opacity: 0.8;
          }
          border: none;
          cursor: pointer;
          font-size: 40px;
          color: $mainClr;
          background-color: transparent;

          @media screen and (max-width: 767px) {
               font-size: 35px;
          }
     }
     &__dot {
          position: absolute;
          display: flex;
          text-align: center;
          align-items: center;
          bottom: 0;
          z-index: 5;
          left: 50%;
          gap: 15px;
          transform: translate(-50%, -50%);
          cursor: pointer;
          @media screen and (max-width: 767px) {
               display: none;
          }
          &--item {
               background-color: rgba(190, 190, 190, 0.8);
               padding: 2px 10px;
               margin-bottom: 6px;
               border-radius: 100rem;
               transition: 0.35s ease;
               &.active {
                    background-color: white;
               }
          }
     }

     &__item {
          width: 100%;
          height: 100%;
          position: relative;
     }
}
