@import "../../scss/variable";

.Not {
     position: fixed;
     width: 100%;
     top: 0;
     z-index: 1;
     height: 100%;
     background-color: white;
     @include flex(center);
     flex-direction: column;
     gap: 20px;
     &__text {
          color: $newMain;
          font-size: 20px;
          font-weight: 700;
          text-transform: uppercase;
     }
     &__link {
          padding: 5px 10px;
          background-color: $mainClr;
          color: white;
          border-radius: 5px;
          font-weight: 500;
          cursor: pointer;
          font-weight: 700;
     }
}
