@import "../../scss/variable";

.remember {
     position: fixed;
     font-size: 10px;
     padding: 2px 18px;
     display: flex;
     justify-content: space-between;
     align-items: center;
     width: 100%;
     color: $TextClr2;
     bottom: 0;
     left: 0;
     right: 0;
     z-index: 999;
     line-height: 1.2;
     background-color: white;
     box-shadow: 0 0 1px $TextClr2;
     &.black {
          background-color: $DarkClr !important;
          box-shadow: 0 0 1px $DarkClr;
     }
     & a {
          color: $TextClr2;
     }
}
.remember__icon {
     display: flex;
     gap: 15px;
     & a {
          transition: 0.35s ease;
          font-size: 16px;
          cursor: pointer;
          color: $TextClr2;
          &:hover:first-child {
               color: rgb(0, 132, 255);
          }
          &:hover:nth-child(2) {
               color: rgb(255, 0, 0) !important;
          }
     }
}
