@import "../../scss/variable";

#contact {
     margin-top: 0px;
     padding-top: 30px;
     padding-bottom: 40px;
     @include flex(center);
     flex-direction: column;
}
.contact__container {
     @include flex(center);
     flex-wrap: wrap;

     // background: $mainClr;
     gap: 30px;
     @media screen and (max-width: 767px) {
          flex-direction: column;
     }
}
.contact {
     &__face {
          width: 300px;
          height: 340px;
          position: relative;
          @media screen and (max-width: 767px) {
               height: 550px;
               width: 300px;
          }
          & iframe {
               @include abs(full);
               border: none;
          }
     }
     &__map {
          width: 1200px;
          height: 540px;
          position: relative;
          @media screen and (max-width: 767px) {
               width: 100%;
               height: 340px;
          }
          & iframe {
               @include abs(full);
          }
     }
     &__icon {
          padding-right: 10px;
          padding-bottom: 10px;
     }
     &__content {
          color: $TextClr;
          flex-wrap: wrap;
          @include flex(around);
          flex-direction: column;
          align-items: flex-start;
          width: 350px;
          font-weight: 600;
          height: 350px;
     }
     &__box {
          padding: 0 20px;
          @media screen and (max-width: 767px) {
               padding: 0 0px;
          }
     }
     &__description a {
          color: $TextClr;
          transition: 0.35s ease-in-out;
          &:hover {
               color: $TextClr;
          }
     }
     &__number {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          align-items: flex-start;
          gap: 15px;
          font-size: 14px;
          font-weight: 700;
          width: 350px;
          height: 340px;
          padding: 20px 0;
          transform: translateX(40px);
          @media screen and (max-width: 767px) {
               flex-direction: column;
               height: auto;
               gap: 20px;
               transform: translateX(0px);
          }
     }
     &__namenum {
          display: flex;
          flex-direction: column;
          gap: 15px;
          color: $TextClr;
          @media screen and (max-width: 767px) {
               flex-direction: column;
          }
     }
     &__name {
          min-width: 180px;
          cursor: pointer;
          transition: 0.35s ease;
          color: $TextClr;
          &:hover {
               color: $TextClr;
               text-decoration: underline;
          }
     }
}
