@import "../../scss/variable";
.avatar__container {
     position: fixed;
     top: 0;
     left: 0;
     width: 65%;
     height: 750px;
     & .avatar__scroll {
          @include flex(center);
          justify-content: flex-start;
          flex-direction: column;
          padding-top: 70px;
          overflow: scroll;
          width: 100%;
          padding-bottom: 100px;
          height: 100vh;
     }
}
.avatar__scroll {
     -ms-overflow-style: none;
     scrollbar-width: none;
}
.avatar__scroll::-webkit-scrollbar {
     display: none;
}
.avatar__avatar {
     @include flex(center);
     flex-direction: column;
     align-items: flex-start;
     padding: 20px 20px;
     justify-content: space-between;
     position: relative;
     width: 100%;

     & p {
          margin-left: 20px;
          color: $TextClr2;
          font-weight: 600;
          font-size: 22px;
     }
}
.avatar__item {
     margin-top: 30px;
     display: flex;
     overflow-x: scroll;
     gap: 20px;

     & img {
          transition: 0.35s ease;
          width: 300px;
          height: auto;
          transform: translateX(-0%);
     }
}
.avatar__item {
     -ms-overflow-style: none;
     scrollbar-width: none;
}
.avatar__item::-webkit-scrollbar {
     display: none;
}
.avatar__button {
     @include abs(center);
     left: 49%;
     top: 60%;
     z-index: 1;
     display: flex;
     justify-content: space-between;
     width: 100%;
     padding: 0 25px;
     &-item {
          padding: 10px 10px;
          cursor: pointer;
          font-size: 20px;
          background-color: $activeClr;
          transition: 0.35s ease;
          color: $background;
          border-radius: 2px;
          &:nth-child(1):hover {
               transform: translateX(-5px);
          }
          &:nth-child(2):hover {
               transform: translateX(5px);
          }
     }
}
