@import "../../scss/variable";

.e__event {
     @include flex(center);
     flex-wrap: wrap;
     padding-top: 100px;
     padding-left: 20%;
     padding-right: 20%;
     gap: 20px;

     @media screen and (max-width: 767px) {
          padding-top: 100px;
          padding-left: 0%;
          padding-right: 0%;
          gap: 15px;
          row-gap: 20px;
     }

     &-name {
          color: white;
          text-transform: uppercase;
          margin: auto;
          font-weight: 700;
     }
     &__container {
          outline: none;
          border: none;
          padding: 4px 12px;
          font-size: 13px;
          box-shadow: 0 0 2px $mainClr;
          border-radius: 100rem;
          background-color: $background;
          color: $TextClr;
          position: relative;
          overflow: hidden;
          font-weight: 500;
          transition: 0.25s ease-in;

          @media screen and (max-width: 767px) {
               font-size: 11px;
          }
          &.activeEvent {
               background-color: $TextClr;
               color: white;
          }

          &::before {
               content: "";
               @include abs(full);
               background-color: $background;
               opacity: 0.07;
               top: 0;
               left: 0;
               width: 0;
               transition: 0.35s ease;
          }
          &:hover {
               &::before {
                    width: 100%;
               }
          }
     }
}
.event__show {
     text-align: center;
     margin: 0 auto;
     margin-top: 30px;
     padding: 10px;
     width: max-content;
     cursor: pointer;
     font-size: 18px;
     color: $mainClr;
}
