@import "../../scss/variable";
.remove__container {
     width: calc(66.5% + 24px);
     height: calc(100vh - 12px);
     overflow: hidden !important;
     position: relative;
     display: flex;
     flex-direction: column;
     align-items: center;
     & p {
          top: 150px;
          z-index: 1;
          position: relative;
          font-weight: 500;
          font-size: 20px;
          color: $TextClr2;
     }
     & .remove__webs {
          position: relative;
          margin: 0 auto;
          font-size: 14px;
          color: $TextClr !important;
          padding-top: 5px;
          display: flex;
          justify-content: center;
     }
     & .remove__box {
          @include abs(center);
          width: 500px;
          height: 500px;
          & .remove__1 {
               position: absolute;
               width: 100%;
               height: 100px;
               top: -2px;
               background-color: white;
          }
          & .remove__2 {
               position: absolute;
               width: 19px;
               height: 100%;
               top: 0px;
               right: 0;
               background-color: white;
          }

          & iframe {
               position: absolute;
               border: none;
               width: 100%;
               height: 100%;
          }
     }
}
