@import "../../scss/variable";

.year__container {
     @include flex(center);
     width: 200px;
     margin: 0 auto;
     height: 30px;
     margin-bottom: 20px;
     margin-top: -20px;
     position: relative;
     overflow: hidden;
}
.year__button {
     @include abs(full);
     @include flex(between);
     z-index: 1;
     &--next,
     &--prev {
          &.disable {
               color: rgba(56, 138, 56, 0.445);
               pointer-events: none;
          }
          transition: 0.35s ease;
          font-size: 30px;
          cursor: pointer;
          background-color: $background;
          padding: 10px;
          color: $TextClr;
     }
}
.year__slider {
     position: absolute;
     width: 100%;
     height: 100%;
     display: flex;
     align-items: center;
     justify-content: flex-start;
     transition: 0.45s cubic-bezier(0, 0.37, 0.85, 1.79);

     &__item {
          white-space: nowrap;
          position: relative;
          min-width: 100%;
          transition: 0.4s ease;
          @include flex(center);
          & p {
               font-weight: 500;
               color: $background;
               background-color: $TextClr;
               padding: 1px 10px;
               letter-spacing: 0.5px;
               border-radius: 3px;
          }
     }
}
