@import "../../scss/variable";

.nav__side {
     &.black {
          background: $DarkClr;
          & .nav__side--list-item {
               color: white !important;
          }
     }
     @media screen and (max-width: 767px) {
          display: none;
     }
     position: fixed;
     right: 2px;
     top: 0;
     width: 75px;
     height: 100vh;
     padding-top: 80px;
     display: flex;
     flex-direction: column;
     z-index: 9;
     &--list {
          gap: 15px;
          white-space: nowrap;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          &-item {
               &.plus > i {
                    &.active {
                         & ~ .nav__side-plus {
                              display: flex;
                         }
                         transform: rotate(-45deg);
                    }
                    transition: 0.15s ease;
                    font-size: 38px;
                    color: $newMain2;
                    position: relative;
               }
               &.activeNav {
                    background: $subClr2 !important;
                    color: $newMain;
               }
               &:hover {
                    background: $activeClr;
               }
               transition: 0.35s ease;
               border-radius: 15px;
               padding: 15px 5px;
               width: 95%;
               display: flex;
               flex-direction: column;
               justify-content: center;
               align-items: center;
               cursor: pointer;
               color: $TextClr2;
               overflow: hidden;
               & > i {
                    font-size: 18px;
                    margin-bottom: 8px;
               }
               & span {
                    font-size: 11.7px;
                    font-weight: 700;
               }
          }
     }
}
.nav__side-plus {
     @include flex(center);
     flex-direction: column;
     position: absolute;
     top: 120px;
     white-space: nowrap;
     right: 100px;
     list-style: none;
     width: 160px;
     border-radius: 10px;
     padding: 15px 0px;
     box-shadow: 0 0 4px $TextClr2;
     display: none;
     background: white;
     & > li {
          color: $TextClr2 !important;
          & span:before {
               content: "";
               position: absolute;
               width: 130px;
               height: 100%;
          }
          & i {
          }
          & a {
               transform: translateX(-2px);
               color: $TextClr2 !important;

               & > span {
                    font-size: 14.5px;
                    font-weight: 500;
                    margin-left: 5px;
               }
          }
          transition: 0.25s ease;
          user-select: none;
          @include flex(center);
          justify-content: flex-start;
          gap: 10px;
          width: 100%;
          text-align: center;
          cursor: pointer;
          padding: 10px 20px;
          font-weight: 500;
          &:hover {
               background: $activeClr;
          }
     }
}

.plus__item {
     padding-left: 10px;
}
.nav__drive {
     & > i {
          padding-right: 6px;
     }
}
