@import "../../scss/variable";
.cs__full {
     @include flex(center);
     width: 65%;
     flex-wrap: wrap;
     gap: 50px;
     margin-top: 120px;
     margin-left: 18px;
     row-gap: 20px;
     @media screen and (max-width: 767px) {
          margin-left: 0px;
          width: 100%;
     }
}
.cs__start {
     position: absolute;
     top: 110px;
     font-weight: 500;
     color: $TextClr2;
     font-size: 18px;
     @include flex(center);
     gap: 10px;
     @media screen and (max-width: 767px) {
          font-size: 14px;
     }
     & span {
          line-height: 12px;
     }
}
.cs__container {
     @include flex(center);
     flex-direction: column;
     margin-top: 20px;
     min-width: 300px;
     gap: 20px;
     margin-bottom: -150px;
     padding-bottom: 120px;

     &:nth-child(7) {
          order: 2;
     }
}
.cs__img {
     margin-top: 50px;
     width: 300px;
     height: 180px;
     position: relative;
     border-radius: 10px;
     overflow: hidden;
     box-shadow: 0 0 4px $TextClr2;
     @media screen and (max-width: 767px) {
          width: 250px;
          height: 170px;
     }

     & b {
          position: absolute;
          right: 6px;
          bottom: 8px;
          color: rgb(68, 255, 208);
          font-size: 12px;
          text-shadow: 0 0 3px $TextClr2;
          font-weight: 500;
     }
     & img {
          @include abs(full);
     }
     & span {
          position: absolute;
          width: 0%;
          height: 4px;
          background-color: rgb(108, 255, 218);
          bottom: 0;
          left: 0;
          border-radius: 5px;
     }
}
.cs__content {
     font-weight: 500;
     font-size: 16px;
     color: $TextClr2;
     position: relative;
}
.cs__btn {
     background-color: $mainClr;
     padding: 2px 8px;
     font-weight: bold;
     font-size: 13px;
     line-height: 24px;
     color: white;
     border-radius: 10px;
     cursor: pointer;
     display: flex;
     gap: 5px;
     justify-content: center;
     align-items: center;
     transition: 0.35s ease;
     &:hover {
          background-color: rgb(0, 163, 95);
     }
     &:nth-of-type(2):hover {
          background-color: rgb(255, 130, 27);
     }
}
