@import "../../scss/variable";
#youtube {
     position: relative;
}
.youtube_img1 {
     display: none;
     position: absolute;
     left: -130px;
     @media screen and (max-width: 767px) {
          display: none;
     }
}
.youtube_img2 {
     position: absolute;

     display: none;
     right: -148px;
     @media screen and (max-width: 767px) {
          display: none;
     }
}
.youtube {
     padding: 80px;
     margin: auto;
     margin-top: 50px;
     width: 1000px;
     margin-bottom: 50px;

     height: 600px;
     @include flex(center);
     position: relative;
     @media screen and (max-width: 767px) {
          width: 450px;
          height: 320px;
     }
     @media screen and (max-width: 467px) {
          width: 320px;
          height: 230px;
     }
     & iframe {
          position: absolute;
          width: 100%;
          height: 100%;
          box-shadow: 0 0 4px $mainClr;
          border: 4px solid $mainClr;
     }
}
