$mainClr: #298a00;
$DarkClr: #084700;
$mainClrA: #09ff0050;
$activeClr: #b4b4b43d;
$background: #ffffff;
$hoverClr: #b2ffa252;
$subClr2: #dbffceea;
$subClr: #166e00e3;
$newMain: rgb(3, 92, 0);
$newMain2: rgb(38, 185, 8);
$newMain3: rgb(115, 255, 0);
$newClr4: #1b5a00;
$TextClr: #116604f8;
$TextClr2: rgba(77, 77, 77, 0.938);

@mixin flex($position) {
     @if $position == "center" {
          display: flex;
          justify-content: center;
          align-items: center;
     }
     @if $position == "between" {
          display: flex;
          justify-content: space-between;
          align-items: center;
     }
     @if $position == "around" {
          display: flex;
          justify-content: space-around;
          align-items: center;
     }
}
@mixin abs($position) {
     @if $position == "full" {
          position: absolute;
          width: 100%;
          height: 100%;
     }
     @if $position == "center" {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
     }
}
