@import "../../scss/variable";

.header {
     position: fixed !important;
     box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.06);
     top: 0;
     left: 0 !important;
     right: 0 !important;
     z-index: 999;
     padding-bottom: 5px;
     height: 60px;
     width: 100% !important;
     transition: 0.15s ease;
     background-color: $newMain;
     @include flex(center);
     box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.06);
     @media screen and (max-width: 767px) {
          justify-content: flex-start;
     }
     &__mam {
          color: rgb(255, 255, 255);
          position: absolute;
          left: 90px;
          font-size: 13.5px;
          font-weight: 500;
          & .header__course {
               border-left: 1px solid white;
               margin-left: 3px;
               padding-left: 5px;
          }
     }

     &.black {
          background-color: $DarkClr !important;
          box-shadow: 0 4px 0px 0 rgba(0, 0, 0, 0) !important;
          border-bottom: 1px solid $DarkClr !important;
          padding: 28px !important;
          & .header__back {
               color: white !important;
          }
     }
}
.header.active {
     & .header__scroll {
          height: 0 !important;
     }
     top: 0px;
     height: 50px;
     transition: 0.15s ease;
     padding: 32px;
     left: 0px;
     right: 0px;
     transition: 0.35s ease;
     border-bottom: 1px solid #f0f0f0;
     box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.06);
     // background-image: linear-gradient(145deg, $mainClr, $newMain);
     background-color: rgb(255, 255, 255);
     // &::before {
     //      content: "";
     //      background-color: $newMain;
     //      width: 100%;
     //      height: 100%;
     //      position: absolute;
     // }
     & .header__h1 {
          color: $mainClr;
     }
     & .header__bar,
     .header__barclose {
          margin-right: 3px;
          color: $mainClr;
     }
     & .header__back {
          color: $mainClr;
          margin-left: 40px;
          user-select: none;
          @media screen and (max-width: 767px) {
               margin-left: 5px;
          }

          & i {
               transition: 0.35s ease;
          }
          &:hover i {
               transform: translateX(-3px);
          }
     }
     @media screen and (max-width: 767px) {
          & .header__list {
               background-color: $background;
               margin-top: 20px;

               box-shadow: 0 0 5px $newMain;
          }
     }

     & .header__list--item {
          color: $TextClr;
          &.active1 {
               position: relative;

               &:after {
                    width: 85%;
               }
          }
          &:after {
               content: "";
               position: absolute;
               bottom: 10px;
               left: 0;
               width: 0%;
               background-color: $mainClr;
               height: 2px;
               opacity: 0.99;
               border-radius: 100rem;
               transition: 0.45s ease;
          }

          &:hover {
               color: $mainClr;
               &:after {
                    width: 70%;
               }
          }
     }
}
.header__scroll {
     position: absolute;
     width: 0%;
     bottom: 0;
     left: 0;
     height: 4px;
     border-radius: 100rem;
     background-color: $newMain3;
     box-shadow: 0 0 5px $newMain3;
}

.header__back {
     @include flex(center);
     gap: 10px;
     cursor: pointer;
     & i {
          transition: 0.35s ease;
     }
     &:hover i {
          transform: translateX(-3px);
     }
     &:active {
          opacity: 0.8;
     }
     & span {
          font-size: 12px;
          @media screen and (max-width: 767px) {
               font-size: 10px;
          }
     }
     position: fixed;
     left: 50px;
     border: none;
     font-size: 20px;
     color: white;

     @media screen and (max-width: 767px) {
          // display: none;
          left: 20px;
     }
}

.header__fixlogo {
     position: fixed;
     width: 42px;
     height: 42px;
     top: 50%;
     position: absolute;
     transform: translateY(-50%);
     left: 25px;
     box-shadow: 0 0 1px $mainClr;
     border-radius: 100rem;
     @media screen and (max-width: 767px) {
          display: block;
          position: absolute;
          opacity: 1;
          top: 50%;
          transform: translateY(-50%);
          box-shadow: 0 0 2px $mainClr;
          border-radius: 100rem;
          left: 30px;
          z-index: 98;
          margin-right: 0px;
          width: 35px;
          height: 35px;
     }
}
.header__h1 {
     position: absolute;
     top: 50%;
     left: 50%;
     transform: translate(-50%, -50%);
     color: $background;
     font-size: 16px;
     display: none;
     @media screen and (max-width: 767px) {
          display: block;
     }
}
.header__bar,
.header__barclose {
     color: white;
     font-size: 30px;
     padding: 20px;
     cursor: pointer;
     display: none;
     position: absolute;
     transition: 0.15s ease;
     right: 20px;
     top: 50%;
     transform: translateY(-50%);
     @media screen and (max-width: 767px) {
          display: block;
          padding: 10px;
          z-index: 99;
     }
     &.active1 {
          opacity: 0;
     }
}
.header__barclose {
     margin-right: 3px;
     z-index: -1;
}
.header__list.active1 {
     transform: scaleY(1);
}
.header__list {
     &.active1 {
          transform: scaleY(1);
     }
     @include flex(center);
     text-decoration: none;
     list-style: none;
     gap: 30px;
     transition: 0.2s ease;
     @media screen and (max-width: 767px) {
          position: absolute;
          top: 50px;
          left: 0;
          transform-origin: 50% 0;
          transform: scaleY(0.00000001);
          flex-direction: column;
          background-color: $mainClr;
          opacity: 0.94;
          width: 100%;
          // margin-top: 307px;
          gap: 0px;
     }

     &--item {
          cursor: pointer;
          font-size: 14px;
          color: white;
          font-weight: 500;
          line-height: 60px;
          position: relative;
          display: none;
          @media screen and (max-width: 767px) {
               display: block;
          }

          &:after {
               content: "";
               position: absolute;
               bottom: 10px;
               left: 0;
               width: 0%;
               background-color: $hoverClr;
               height: 2px;
               border-radius: 100rem;
               transition: 0.45s ease;
          }

          &:hover {
               color: $hoverClr;
               &:after {
                    width: 70%;
               }
          }
     }
     &--item.active1 {
          color: white;
          &:after {
               width: 85%;
          }
     }
}
.header__title__h1 {
     font-size: 22px;
     color: $mainClr;
     @media screen and (max-width: 767px) {
          display: none;
     }
}
