@import "../../scss/variable";

.showmore {
     @include flex(center);
     gap: 5px;
     padding-top: 30px;
     padding-bottom: 120px;
     @media screen and (max-width: 767px) {
     }

     &__btn {
          font-size: 18px;
          color: $mainClr;
          cursor: pointer;
     }
     &__text {
          color: $TextClr;
          font-size: 15px;
          font-weight: 500;
          transition: 0.35s ease;
          cursor: pointer;

          &:hover {
               text-decoration: underline;
          }
     }
}
