@import "./scss/variable";

html {
     box-sizing: border-box;
     background-color: rgb(255, 255, 255);
     transition: 0.5s ease-out;
}
body {
     overflow-x: hidden;
     font-family: "Roboto", sans-serif;
     padding-right: 80px;
     @media screen and (max-width: 767px) {
          padding-right: 0px;
     }
}
* {
     &::selection {
          background-color: $newClr4;
          color: white;
     }
     margin: 0;
     padding: 0;
}
*,
*:before,
*:after {
     box-sizing: border-box;
}
// *[title] {
//      color: white;
// }
input,
textarea,
select,
button {
     outline: none;
}
input {
     line-height: normal;
}
label,
button {
     cursor: pointer;
}
a {
     text-decoration: none;
}
img {
     display: block;
     max-width: 100%;
     user-select: none;
}
/* width */
::-webkit-scrollbar {
     width: 3px;
     height: 5px;
}

/* Track */
::-webkit-scrollbar-track {
     box-shadow: inset 0 0 2px $subClr2;
     border-radius: 100rem;
     transition: 0.35s ease;
}

/* Handle */
::-webkit-scrollbar-thumb {
     background: $mainClr;
     border-radius: 100rem;

     transition: 0.35s ease;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
     background: $mainClr;
     transition: 0.35s ease;
}
