@import "../../scss/variable";

.heading-center {
     margin-top: 50px;
     padding-top: 10px;
     @include flex(center);
     flex-direction: column;
     @media screen and (max-width: 767px) {
          margin-top: 50px;
     }
}
.heading {
     color: $TextClr;
     font-size: 34px;
     position: relative;
     letter-spacing: 1px;
     @media screen and (max-width: 767px) {
          font-size: 24px;
     }
     @media screen and (max-width: 367px) {
          font-size: 18px;
     }

     & span {
          position: absolute;
          bottom: -7px;
          left: -10px;
          height: 2px;
          width: 20%;
          background-color: $TextClr !important;
     }
}
.sub {
     color: $TextClr;
     margin: 20px 0;
}
