@import "../../scss/variable";
.skill {
     &__container {
          position: fixed;
          right: 20px;
          top: 0;
          width: 35%;
          height: 100vh;
          display: flex;
          padding-top: 20px;
          flex-direction: row-reverse;
          @media screen and (max-width: 767px) {
               display: none;
          }
     }
     &__avatar {
          display: flex;
          justify-content: flex-start;
          align-items: center;
          padding-top: 140px;
          width: 100%;
          height: 100vh;
          flex-direction: column;
          border-left: 2px solid $activeClr;
          gap: 30px;
          & .skill__button {
               transition: 0.35s ease;

               &.active1 {
                    background-color: $mainClr;
                    color: white;
                    box-shadow: 0 0 4px $TextClr2;
               }
               cursor: pointer;
               font-weight: 400;
               background-color: white;
               padding: 3px 10px;
               color: $TextClr2;
               box-shadow: 0 0 2px $TextClr2;
               line-height: 1.2;
               border-radius: 5px;
               font-size: 15.5px;
               &.w {
                    min-width: 230px;
               }
               & span {
                    padding-left: 5px;
               }
               &:hover {
                    opacity: 0.95;
               }
          }
     }
}
.skill__h6 {
     font-weight: 500;
     font-size: 17px;
     margin-bottom: -10px;
     padding-top: 10px;
     color: $TextClr2;
}
