@import "../../scss/variable";
.logo__container {
     position: fixed;
     left: 0;
     top: 10px;
     width: 65%;
     height: 700px;
     overflow: scroll;
     display: flex;
     gap: 20px;
     flex-direction: column;
}
.logo__container {
     -ms-overflow-style: none;
     scrollbar-width: none;
}
.logo__container::-webkit-scrollbar {
     display: none;
}
.logo__png {
     display: flex;
     justify-content: space-evenly;
     align-items: center;
     height: 150px;
     min-width: 320px;
     gap: 40px;

     box-shadow: 0 0 2px $mainClr;
     border-radius: 5px;

     & .logo__item {
          width: 90px;
          height: 90px;
          gap: 10px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
     }
     & p {
          white-space: nowrap;
          color: $TextClr2;
          font-weight: 500;
     }
}
.logo__icon {
     & a {
          color: $subClr;
          font-weight: 500;
          font-size: 22px;
          transition: 0.35s ease;
          &:hover {
               color: rgb(0, 175, 93);
          }
     }
}
.logo__scroll {
     margin-top: 100px;
     margin-bottom: 100px;
     display: flex;
     justify-content: center;
     align-items: center;
     gap: 30px;
     flex-wrap: wrap;
}
