@import "../../scss/variable";

#hosting {
     width: 100%;
     flex-direction: column;
     @include flex(center);
}
.host__container {
     @include flex(center);
     flex-wrap: wrap-reverse;
     gap: 20px;
     width: 100%;
     margin-bottom: 30px;
}
.host {
     &__name {
          color: rgb(88, 88, 88);
          font-size: 14px;
          line-height: 1.9;
     }
     &__position {
          color: rgb(88, 88, 88);
          font-size: 13px;
          transform: translateY(10px);
          transition: 0.3s cubic-bezier(0, 0.37, 0.85, 1.79);
     }
     &__icon {
          transition: 0.3s ease;
          font-size: 24px;
          color: $mainClr;
          transform: translateY(40px);

          transition: 0.3s cubic-bezier(0, 0.37, 0.85, 1.79);
     }
     &__content {
          @include flex(around);
          flex-direction: column;
          flex: 1;
     }
     &__box {
          border-radius: 10px;
          overflow: hidden;
          @include flex(between);
          flex-direction: column;
          min-width: 200px;
          height: 250px;
          box-shadow: 0 0 4px $mainClr;
          transition: 0.3s ease;
          &:nth-child(3) {
               min-width: 210px;
               height: 260px;
               @media screen and (max-width: 767px) {
                    order: 1;
               }
          }
          &:hover {
               transition-delay: 0.35s;
               transform: translateY(-7px);
               box-shadow: 0 0 4px $mainClr;
               & .host__name {
                    color: $subClr;
                    transition-delay: 0.35s;
               }
               & .host__icon {
                    color: rgb(0, 158, 197);
                    transform: translateY(0px);
               }
               & .host__position {
                    transform: translateY(0px);
               }
          }
     }
     &__img {
          position: relative;
          width: 100%;
          height: 160px;
          & img {
               @include abs(full);
          }
     }
}
.host__active {
     font-size: 14px;
     top: 60px;
     position: relative;
     background-color: $newMain;
     color: white;
     padding: 4px 8px;
     border-radius: 5px;
     margin-bottom: 15px;
}
.host__end {
     font-size: 14px;

     top: 60px;
     position: relative;
     background-color: $TextClr2;
     color: white;
     padding: 4px 8px;
     border-radius: 5px;
     margin-bottom: 15px;
}
